import React, {useMemo,useRef, useState} from 'react';
import axios from "axios";
import ReactQuill, { Quill } from "react-quill";
import "react-quill/dist/quill.snow.css";

const icons = Quill.import("ui/icons");
icons["bold"] = "<span>B</span>";

function Park(props) {

    const [projectCode, setProjectCode] = useState(''); //     
    const [selectedStacks, setSelectedStacks] = useState([]); //      

    // quill 
    const quillRef = useRef()
    const [content, setContent] = useState("")
    // quill 



    const handleProjectName = (event) => {
        const selectedValue = event.target.value;
        setProjectCode(selectedValue); //     
        setSelectedStacks((prevStacks) => [...prevStacks, selectedValue]); //     
    };


    const toyRegistered = e =>{

    }

    const modules = useMemo(() => {
        return {
            toolbar: {
                container: [
                    [{ header: [1, 2, 3, false] }],
                    ["bold", "italic", "underline", "strike"],
                    ["blockquote"],
                    [{ list: "ordered" }, { list: "bullet" }],
                    [{ color: [] }, { background: [] }],
                    [{ align: [] }, "link", "image"],
                ],
            },
        }
    }, [])

    return (
        <div className={"container my-3"}>
            <form onSubmit={toyRegistered}>
            <div className={"row mb-5"}>
                <div className={"col-sm-12 d-flex "}>
                    {/*  /   */}
                    <div className={"col-sm-6 d-flex"}>
                        <div className={"col-sm-3"}>
                            <label htmlFor={"project-name"} className={"form-label"}> :</label>
                        </div>
                        <div className={"col-sm"}>
                            <input type={"text"} className={"form-control"} id={"project-name"} placeholder={"  "}/>
                        </div>
                    </div>
                    <div className={"col-sm-6 d-flex "}>
                        <div className={"col-sm-3"}>
                            <label htmlFor={"project-image"} className={"form-label"}> :</label>
                        </div>
                        <div className={"col-sm"}>
                            <input type={"file"} className={"form-control"} id={"project-image"}/>
                        </div>
                    </div>
                </div>
            </div>
            <div className={"row mb-5"}>
                {/*   /   */}
                <div className={"col-sm-6 d-flex"}>
                    <div className={"col-sm-3"}>
                        <label htmlFor={"project-code"} className={"form-label"}>  :</label>
                    </div>
                    <div className={"col-sm"}>
                        <select id={"project-code"} className={"form-select"} name={"projectCode"} onChange={handleProjectName}>
                            <option value={"SpringBoot"}>SpringBoot</option>
                            <option value={"BootStrap"}>BootStrap</option>
                            <option value={"JS"}>JS</option>
                            <option value={"React"}>React</option>
                            <option value={"Android"}>Android</option>
                        </select>
                    </div>
                </div>
                <div className={"col-sm-6 d-flex"}>
                    <div className={"col-sm-3"}>
                        <label htmlFor={"code-store"} className={"form-label"}>  :</label>
                    </div>
                    <div className={"col-sm"}>
                        <input
                            type={"text"} className={"form-control"} id={"code-store"} value={selectedStacks.join(', ')} readOnly />
                    </div>
                </div>
            </div>
            <div className={"row mb-5"}>
                {/*  /   */}
                <div className={"col-sm-6 d-flex"}>
                    <div className={"col-sm-3"}>
                        <label htmlFor={"total-person"} className={"form-label"}></label>
                    </div>
                    <div className={"col-sm d-flex"}>
                        <input id={"total-person"} className={"form-control me-3"}/><span></span>
                    </div>
                </div>
                <div className={"col-sm-6 d-flex"}>
                    <div className={"col-sm-3"}>
                        <label htmlFor={"level"} className={"form-label"}></label>
                    </div>
                    <div className={"col-sm"}>
                        <select className={"form-select"} id={"level"} name={"levels"}>
                            <option value={"1"}>1</option>
                            <option value={"2"}>2</option>
                            <option value={"3"}>3</option>
                            <option value={"4"}>4</option>
                            <option value={"5"}>5</option>
                        </select>
                    </div>
                </div>
            </div>
            <div className={"row mb-5"}>
                <div className={"col-12"}>
                    <p className={"fs-3 fw-bold text-start"}> </p>
                </div>
                <div name={"detailContent"}>
                    {/*   quill       .  */}
                    <ReactQuill
                        style={{ width: "auto", height: "600px" }}
                        placeholder="    "
                        theme="snow"
                        ref={quillRef}
                        value={content}
                        onChange={setContent}
                        modules={modules}
                    />
                </div>
            </div>
            <div className={"row mt-5"}>
                <div className={"col-sm-12"}>
                    <div className={"d-grid justify-content-md-end"}>
                        <button type={"submit"} className={"btn btn-primary"} onClick={()=>{
                            axios.post("http:localhost:8080/pi/park")
                                .then(()=>{
                                    alert('');
                                })
                                .catch(()=>{
                                    alert('');
                                })
                        }}></button>
                    </div>
                </div>
            </div>
            </form>
        </div>
    )
}

export default Park;