import React, {useEffect} from 'react';
import axios from "axios";
import {Link} from "react-router-dom";
import ChallengeListTable from "./ChallengeListTable";
import ChallengeListSidebar from "./ChallengeListSidebar";

function ChallengeList(props) {

    return (
        <div className={'container-sm my-3'}>
            <div className={'row'}>
                <div className={'col-sm-9'}>
                    <h1>  </h1>
                    <div className={'row'}>
                        <div className={'col-3'}>
                            <select name="" id="" className={'form-select me-2'}>
                                <option value=""></option>
                                <option value=""> </option>
                                <option value="">  </option>
                            </select>
                        </div>
                        <div className={'col-3'}>
                            <select name="" id="" className={'form-select me-2'}>
                                <option value=""></option>
                                <option value="">0</option>
                                <option value="">1</option>
                                <option value="">2</option>
                                <option value="">3</option>
                                <option value="">4</option>
                                <option value="">5</option>
                            </select>
                        </div>
                        <div className={'col-6'}>
                            <input type="text" className={'form-control'} placeholder={' '}/>
                        </div>
                    </div>

                    <ChallengeListTable/>
                </div>
                <div className={'col-sm-3'}>
                    <ChallengeListSidebar/>
                    <Link to={`http://localhost:3000/lee?idx=1`} className={'btn btn-primary'}> </Link>
                </div>
            </div>

        </div>
    )
}

export default ChallengeList;